:root {
  --primary-color: #4dc2e5; /* Teal Blue */
  --secondary-color: #498bb2; /* Darker Teal */
  --text-color: #ffffff; /* White */
  --background-color: #cecece; /* Light Gray */
}

.App {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid var(--secondary-color);
  border-radius: 4px;
  background-color: var(--background-color);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

header {
  text-align: center;
  margin-bottom: 20px;
  color: var(--primary-color);
}

form {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 10px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  color: var(--primary-color);
}

.form-group input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid var(--secondary-color);
  border-radius: 4px;
  background-color: var(--text-color);
  color: var(--secondary-color);
}

button {
  background-color: var(--primary-color);
  color: var(--text-color);
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: var(--secondary-color);
}

.schedule {
  margin-top: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid var(--secondary-color);
  padding: 8px;
  text-align: right;
}

th {
  background-color: var(--primary-color);
  color: var(--text-color);
}

h2 {
  margin-top: 0;
  color: var(--primary-color);
}
